.wrap-nav {
    display: flex;
    justify-content: space-between;
    height: 100%;
    align-items: center;
    padding: 0 30px;
}
nav.header-nav {
    width: 100%;
    /* padding: 20px 25px 0 25px; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 18px;
    color: white;
}
nav a {
    text-decoration: none;
    color: white;
}

nav li {
    list-style-type: none;
}
.logo-wrap {
    width: 75px;
    height: 75px;
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.logo {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
    position: absolute;
    top: 0;
    left: 0;
}

.wrap-options {
    display: flex;
    gap: 30px;
}

.contact-me {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 5px solid white;
    background: #ffc600;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 20px;
    right: 20px;
    cursor: pointer;
    z-index: 1;
}

.contact-me a {
    text-decoration: none;
    color: #282c34;
    font-weight: bold;
    font-size: 15px;
}
.mobile {
    fill: white;
    width: 35px;
    height: 35px;
    filter: drop-shadow(0px 0px 8px rgb(255 255 255));
}
@media (max-width: 768px) {
    .logo-wrap {
        width: 65px;
        height: 65px;
        position: absolute;
        left: 10px;
        top: 17%;
    }

    .wrap-options {
        width: 100%;
        justify-content: space-evenly;
    }
    .contact-me {
        width: 60px;
        height: 60px;
        top: 70px;
    }

    nav a.active svg {
        fill: #ffc600;
    }
}
